
import React, { Component } from 'react';
import './App.css';
import Papa from 'papaparse';


class App extends Component {
  constructor() {
    super()
    this.state = {
      data: []
    }
  }

  componentDidMount() {
      Papa.parse('https://docs.google.com/spreadsheets/d/e/2PACX-1vTPva1TqjJb1q71rdIyiL8kCTg1ErWP8OWYJQqDLWZhNPP43EechxS7r7mOzKL43En-FHBx0Ql0J0Lp/pub?gid=0&single=true&output=csv', {
      download: true,
      header: true,
      complete: function(results) {
        this.setState( {'data': results.data } )
      }
    })
  }

  render() {
    console.log('updated state --->', this.state)
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Эвенты-хуенты</h1>
        </header>
      </div>
    );
  }
}

export default App;